@import "../../style/breakPoints.scss";

$buttonColor: #3c404a;
$textColor: #fff;

.button_box {
  width: 7%;
  margin-top: 4%;
}

.button {
  background: $buttonColor;
  width: 100%;
  border: none;
  border-radius: 2px;
  padding: 5px 0;
  color: $textColor;
  box-shadow: 0px 2px 21px 4px rgba(3, 2, 12, 0.65);
  cursor: pointer;
}

@media only screen and (max-width: $xl) {
  .button_box {
    width: 15%;
  }
}
