@import "../../style/breakPoints.scss";

$colorText: #fff;

.diagram_box {
  margin: 7% 0;

  .data_time_box {
    width: 98%;
  }

  .description {
    margin: 0;
    color: $colorText;
    font-weight: 300;
    opacity: 0.5;
  }

  .data_box {
    display: flex;
    width: 97%;
    justify-content: space-between;

    .data {
      margin: 0 0 5px;
      color: $colorText;
      font-weight: 300;
      font-size: 14px;
    }

    .time {
      margin-right: 2.45%;
    }
  }
}

.forecast_now {
  display: flex;
  width: 50%;
  justify-content: space-between;

  .forecast_text {
    color: $colorText;
    font-weight: 600;

    span {
      opacity: 0.5;
      font-weight: 300;
    }
  }
}

@media only screen and (max-width: $md) {
  .forecast_now {
    width: 100%;
    margin-top: 50px;
  }

  .diagram_box {
    .data_box {
      .data {
        font-size: 10px;
      }
      .time {
        margin-right: 0;
        font-size: 14px;
      }
    }
  }
}

@media only screen and (max-width: $sm) {
  .line_chart {
    display: none;
  }
  .diagram_box {
    .data_box {
      .data {
        font-size: 7px;
      }
      .time {
        margin-right: 0;
        font-size: 12px;
      }
    }
  }
}
